import jstz from 'jstimezonedetect';
import LoginButton from './LoginButton';
import { generateUrl, imagePath } from '@nextcloud/router';
export default {
  name: 'LoginForm',
  components: {
    LoginButton: LoginButton
  },
  props: {
    username: {
      type: String,
      default: ''
    },
    redirectUrl: {
      type: [String, Boolean],
      default: false
    },
    errors: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    messages: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    throttleDelay: {
      type: Number,
      default: 0
    },
    invertedColors: {
      type: Boolean,
      default: false
    },
    autoCompleteAllowed: {
      type: Boolean,
      default: true
    },
